import PropTypes from "prop-types";
import styled from "styled-components";

const CONTAINER = styled.div`
    display: flex;
    flex-direction: column;
    ${(props) => props.customStyle}
`

const LABEL = styled.label`
    ${(props) => props.customStyle}
`

const SELECT = styled.select`
    ${(props) => props.customStyle}
`

const DEFAULTOPTION = styled.option`
    ${(props) => props.customStyle}
`

const OPTION = styled.option`
    ${(props) => props.customStyle}
`

function Select ({defaultOption, options, selectTitle}) {

    const label = selectTitle.replace(" ", "-").toLowerCase()

    return (
        <CONTAINER customStyle = {styleContainer}>
            {selectTitle !== "" && <LABEL htmlFor = {label} customStyle = {styleLabel}>{selectTitle}</LABEL>}
            <SELECT id = {label} customStyle = {styleSelect}>
                {defaultOption !== "" && <DEFAULTOPTION value = "" customStyle = {styleDefaultOption}>{defaultOption}</DEFAULTOPTION>}
                {options.map((option, index) =>(
                    <OPTION
                        key = {index}
                        value = {option}
                        customStyle = {styleOption}
                    >
                        {option}
                    </OPTION>
                ))}
            </SELECT>
        </CONTAINER>
    )
}

Select.propTypes = {
    defaultOption: PropTypes.string,
    options: PropTypes.array,
    selectTitle: PropTypes.string,
    styleContainer: PropTypes.object,
    styleDefaultOption: PropTypes.object,
    styleLabel: PropTypes.object,
    styleOption: PropTypes.object,
    styleSelect: PropTypes.object
}
  
Select.defaultProps = {
    defaultOption: "",
    options: ["Option 1", "Option 2", "Option 3"],
    selectTitle: "Title"
}

export default Select